<template>
   <div>
    <div class="list-view" ref="container" @scroll="handleScroll">
      <div class="list-view-phantom" ref="clientHeight" :style="{ height: contentHeight + 'px' }"></div>
        <ul ref="content" class="list-view-content">
            <li class="list-view-item" :style="{ height: itemHeight + 'px' }" :key="index" v-for="(val, index) in list">
            {{ val }}
            </li>
        </ul>
      </div>
  </div>
</template>

<script>
export default {
     name: 'ListView',
  computed: {
    contentHeight() {
      // 
      return this.data.length * this.itemHeight;
    }
  },

  mounted() {
    this.getData();
    this.update();
  },

  data() {
    return {
      data: [1,2,3,5,6,6,7,8,9,0,1,2,2,3,4,4,5,6,7,7,8,9,0,1,2,3,5,6,6,7,8,9,0,1,2,2,3,4,4,5,6,7,7,8,9,0,1,2,3,5,6,6,7,8,9,0,1,2,2,3,4,4,5,6,7,7,8,9,0,,1,2,3,5,6,6,7,8,9,0,1,2,2,3,4,4,5,6,7,7,8,9,0, 1,2,3,5,6,6,7,8,9,0,1,2,2,3,4,4,5,6,7,7,8,9,0,1,2,3,5,6,6,7,8,9,0,1,2,2,3,4,4,5,6,7,7,8,9,0], // 
      itemHeight: 30, // 
      list: []
    };
  },

  methods: {
    update(scrollTop = 0) {
      // 
      const count = Math.ceil(this.$el.clientHeight / this.itemHeight);
      // 
      const start = Math.floor(scrollTop / this.itemHeight);
      // 
      const end = start + count;

      //  Vue.js 
      this.list = this.data.slice(start, end);

      //  top  transform 
      this.$refs.content.style.webkitTransform = `translate3d(0, ${ start * this.itemHeight }px, 0)`;
    },
    handleScroll(e) {
      // 
      const scrollTop = this.$refs.container.scrollTop;
      this.update(scrollTop);
    },
    getData() {
      //
      let data = [];
      for (let i = 0; i < 1000000; i++) {
          data.push(` ${i} `)
      }
      this.data = [...data];
    }
  }
}

</script>

