import Vue from 'vue';

export default function (Component,props){
  //1.component  new vue extend
  const vm = new Vue({
    render(h){
      return h(Component,props);//div,{}
    }
  }).$mount();

  //dom
  document.body.appendChild(vm.$el);

  //
  const comp = vm.$children[0];
  comp.remove=()=>{
    document.body.removeChild(vm.$el);
    vm.$destroy(); //dom
  }

  return comp;
}
